* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

body {
    font-family: 'Roboto';
    font-weight: 400;
    color: #777777;
    font-size: 16px;
    letter-spacing: 2px;
    padding: 50px;
    background-color: white; }

.container {
    background-color: #f7f7f7;
    height: 500px;
    width: 100%; }

.header {
    position: relative;
    height: 95vh;
    background:linear-gradient(90deg, rgba(186, 133, 84, 0.7) 0%, rgba(1, 1, 1, 0.7) 100%), url(../img/header-background.jpeg) center / cover no-repeat {}
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
    &__logo {
        position: absolute;
        left: 50px;
        top: 50px;
        width: 56px;
        height: 70px; } }
.title-first {

    &__main {
        font-family: "Lato";
        font-weight: 800;
        font-size: 50px;
        letter-spacing: 0.2em; }
    &__secondary {
        font-family: "Lato";
        font-weight: 300;
        font-size: 40px;
        letter-spacing: 0.12em;
        color: #fff; } }
